import {Component, OnDestroy, OnInit} from '@angular/core';
import * as baseData from '../../content/baseData';
@Component({
  selector: 'app-paragraph-typing',
  templateUrl: './paragraph-typing.component.html',
  styleUrls: ['./paragraph-typing.component.css']
})
export class ParagraphTypingComponent implements OnInit, OnDestroy {
  contentExample = [];
  step = 1;
  information = [];
  inputContent = baseData.baseAlphabet;
  currentInputContent = '';
  currentInputContentDesc = '';
  currentInputStartTime = new Date();
  currentInputInterval;
  currentInputPassedSecond = 0;
  beforeSuccessTime = 0;
  timeLimit = 4;
  successCount = 0;
  falseCount = 0;
  problemCount = 0;
  keyboardStyle = {backgroundColor: 'rgb(255,255,255'};
  successPercentage = 100;
  averageSecond = 0;
  isTest = false;
  testMessage = '';
  // ----
  testTimeLimit = {oneStep: 3, twoStep: 3, threeStep: 5};
  falseSound = new Audio('assets/music/false_sound.wav');
  successSound = new Audio('assets/music/tick.mp3');
  constructor() {
    this.oneStepClicked();
  }

  ngOnInit() {
  }
  readEnter(event) {
    this.contentExample.push(event.target.value);
    this.checkEnter(event.target.value);
    this.contentExample = this.contentExample.filter((value, index, array) => {
      if (array.length - 5 > index) {
        return false;
      }
      return true;
    })
    event.target.value = '';
    if (this.isTest) {
      this.checkTestEnd();
    }
  }
  startInputSystem() {
    if (this.currentInputInterval) {
      clearInterval(this.currentInputInterval);
    }
    this.currentInputStartTime = new Date();
    this.currentInputInterval = setInterval(() => {
      this.currentInputPassedSecond = (new Date().getTime() - this.currentInputStartTime.getTime()) / 1000;
      this.otherAttributeSetting();
      if (this.currentInputPassedSecond > this.timeLimit) {
        this.playFailSound();
        if (this.isTest) {
          this.checkTestEnd();
        }
        this.falseCount = this.falseCount + 1;
        this.currentInputStartTime = new Date();
        this.contentSetting();
      }
    }, 100);
  }
  contentSetting() {
    const index = this.getRandomContentIndex();
    this.currentInputContent = this.inputContent[index].word;
    this.currentInputContentDesc = this.inputContent[index].desc;
    this.problemCount += 1;
  }
  otherAttributeSetting() {
    this.percentageSetting();
    this.keyboardStyleSetting();
  }
  percentageSetting() {
    if (this.successCount !== 0 || this.falseCount !== 0) {
      this.successPercentage = Math.floor((this.successCount / (this.successCount + this.falseCount)) * 100);
    }
  }
  keyboardStyleSetting() {
    const color = 255 - 70 * this.currentInputPassedSecond;
    this.keyboardStyle = {backgroundColor: `rgb(${color},${color},${color}`};
  }
  getRandomContentIndex(): number {
    const length = this.inputContent.length;
    const index = Math.floor((Math.random() * (length + 2) + Math.random() * 3)  % length);
    return index;
  }
  successHandler() {
    this.beforeSuccessTime = this.currentInputPassedSecond;
    this.successCount = this.successCount + 1;
    this.averageSecond = ((this.averageSecond * (this.successCount - 1)) + this.beforeSuccessTime) / this.successCount;
  }
  checkEnter(enterInput) {
    if (enterInput === this.currentInputContent) {
      this.playSuccessSound();
      this.successHandler()
      this.contentSetting();
      this.startInputSystem();
    } else {
      this.playFailSound();
      this.falseCount = this.falseCount + 1;
    }
  }
  speedChange(type) {
    if (type === 'up') {
      if (this.timeLimit - 1 >= 1) {
        this.timeLimit = this.timeLimit - 1;
      }
    } else {
      if (this.timeLimit + 1 <= 5) {
        this.timeLimit = this.timeLimit +  1;
      }
    }
  }
  oneStepClicked(test?) {
    this.step = 1;
    this.inputContent = baseData.baseAlphabet;
    this.problemCount = 0;
    this.information = ['기본적인 타자를 연습할거야', '천천히 연습해보도록 해', '영타를 입력하고 싶으면 [한/영] 키를 눌러 봐'];
    this.contentSetting();
    this.startInputSystem();
    this.changeTestMessage();
  }
  twoStepClicked(test?) {
    this.step = 2;
    this.inputContent = baseData.baseAlphabetUpper;
    this.problemCount = 0;
    this.information = ['이번엔 shift 와 함께 입력해보자', '천천히 연습해보도록 해'];
    this.contentSetting();
    this.startInputSystem();
    this.changeTestMessage();
  }
  threeStepClicked(test?) {
    this.step = 3;
    this.inputContent = baseData.baseAlphabetLowerUpper;
    this.problemCount = 0;
    this.information = ['이제 소문자와 대문자가 모두 나올거야', '어쩔 땐 shift를 눌러야 하고, 어쩔 땐 누르면 안돼!'];
    this.contentSetting();
    this.startInputSystem();
    this.changeTestMessage();
  }
  testStart() {
    this.resetStartInformation();
    this.isTest = true;
    if (this.step === 1) {
      this.timeLimit = this.testTimeLimit.oneStep;
      this.oneStepClicked(true);
    } else if (this.step === 2) {
      this.timeLimit = this.testTimeLimit.twoStep;
      this.twoStepClicked(true);
    } else if (this.step === 3) {
      this.timeLimit = this.testTimeLimit.threeStep;
      this.threeStepClicked(true);
    }
  }
  checkTestEnd() {
    if (this.successCount + this.falseCount >= 30) {
      this.testEnd();
    }
    if (this.falseCount >= 5) {
      this.testEnd();
    }
  }
  testEnd() {
    clearInterval(this.currentInputInterval);
    this.isTest = false;
    if (this.falseCount >= 5) {
      alert('실패');
    } else {
      alert('성공');
    }
    this.resetStartInformation();
  }
  resetStartInformation() {
    this.problemCount = 0;
    this.successCount = 0;
    this.falseCount = 0;
    this.beforeSuccessTime = 0;
    this.percentageSetting();
  }
  changeTestMessage() {
    const limit = this.getTimeLimit();
    this.testMessage = `단계는 ${this.step} 단계! 각 단어의 제한시간은 ${limit}초입니다! \r\n 30개 중 25개 이상을 맞춰야 해요!`;
  }
  getTimeLimit(): number {
    if (this.step === 1) {
      return this.testTimeLimit.oneStep;
    } else if (this.step === 2) {
      return this.testTimeLimit.twoStep;
    } else if (this.step === 3) {
      return this.testTimeLimit.threeStep;
    }
  }
  playFailSound() {
    this.falseSound.pause();
    this.falseSound.play();

  }
  playSuccessSound() {
    this.successSound.pause();
    this.successSound.play();
  }
  pausePractice() {
    clearInterval(this.currentInputInterval);
  }
  reStart() {
    if (this.step === 1) {
      this.oneStepClicked();
    } else if (this.step === 2) {
      this.twoStepClicked();
    } else if (this.step === 3) {
      this.threeStepClicked();
    }
  }
  ngOnDestroy(): void {
    clearInterval(this.currentInputInterval);
  }
}

